import API from '@/utils/api.js'

export default {
  state: {
    user: null
  },
  mutations: {
    SET_USER: (state, payload) => {
      state.user = payload
    }
  },
  actions: {
    login: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/manager/login`,
          method: 'POST',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    getAccount: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/manager/${payload}`,
          method: 'GET'
        }).then(res => {
          const data = res.data
          commit('SET_USER', data.data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
  },
  namespaced: true
};
