import API from '@/utils/api.js'

export default {
  state: {
    list: [],
    total: 0,
    detaile: null
  },
  mutations: {
    SET_LIST: (state, payload) => {
      state.list = payload
    },
    SET_DETAIL: (state, payload) => {
      state.detail = payload
    },
    SET_TOTAL: (state, payload) => {
      state.total = payload
    }
  },
  actions: {
    getList: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/news/list`,
          method: 'GET',
          params: payload
        }).then(res => {
          const data = res.data
          commit('SET_LIST', data.data.data)
          commit('SET_DETAIL', data.total)
          resolve({
            list: data.data.data,
            total: data.data.total
          })
        }).catch(err => {
          reject(err)
        })
      })
    },
    getDetail: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/news/${payload}`,
          method: 'GET',
        }).then(res => {
          const data = res.data
          commit('SET_DETAIL', data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    add: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/news`,
          method: 'POST',
          data: payload
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    update: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/news/${payload.id}`,
          method: 'PUT',
          data: payload.data
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    delete: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/news/${payload}`,
          method: 'DELETE'
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
  },
  namespaced: true
};
