import Vue from 'vue'
import VueRouter from 'vue-router'
import menu from './menu'
import store from '@/store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home',
    component: () => import('@/layout'),
    meta: {
      auth: true
    },
    children: menu
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/Login')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: '/apeces/',
  routes
})

router.beforeEach(async (to, from, next) => {
  if (to.matched.some(record => record.meta.auth)) {
    const auth = sessionStorage.getItem("is_login")
    const user_id = Number(sessionStorage.getItem('user_id'))
    if (!auth) {
      router.push({path: '/login'})
    } else {
      const user = store.state.login.user
      if (!user) {
        const data = await store.dispatch('login/getAccount', user_id)
        const isShop = !!data.shop
        if (to.name === 'Home') {
          next()
        }
        if (isShop && to.matched.some(record => record.meta.role)) {
          router.push({
            name: 'Home'
          })
        } else if (!isShop && to.matched.some(record => !record.meta.role)) {
          router.push({
            name: 'Home'
          })
        }
        next()
      } else {
        if (to.matched.some(record => record.meta.role) && user.shop) {
          router.push({
            name: 'Home'
          })
        } else {
          next()
        }
      }
    }
  } else {
    next()
  }
})

const originalPush = VueRouter.prototype.push

VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

export default router
