import API from '@/utils/api.js'

export default {
  state: {
    accountData: null
  },
  mutations: {
    SET_DATA: (state, payload) => {
      state.accountData = Object.assign({}, payload)
    }
  },
  actions: {
    getData: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/order/count`,
          method: 'GET',
          params: payload
        }).then(res => {
          const data = res.data
          commit('SET_DATA', data.data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    }
  },
  namespaced: true
};
