import API from '@/utils/api.js'

export default {
  state: {
    categoryResult: {},
    bookResult: {}
  },
  mutations: {
    SET_BOOK_RESULT: (state, payload) => {
      state.bookResult = Object.assign({}, payload)
    },
    SET_CATEGORY_RESULT: (state, payload) => {
      state.categoryResult = Object.assign({}, payload)
    }
  },
  actions: {
    //分类模板
    categoryTemplate: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/category/template`,
          method: 'GET',
          responseType: "blob",
        }).then(res => {
          const data = res.data
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    //书籍模板
    bookTemplate: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/book/template`,
          method: 'GET',
          responseType: "blob",
        }).then(res => {
          const data = res.data
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    //分类导入
    categoryImport: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/category/import`,
          method: 'POST',
          data: payload
        }).then(res => {
          const data = res.data
          commit('SET_CATEGORY_RESULT', data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
    //书籍导入
    bookImport: ({commit}, payload) => {
      return new Promise((resolve, reject) => {
        API.request({
          url: `/book/import`,
          method: 'POST',
          data: payload
        }).then(res => {
          const data = res.data
          commit('SET_BOOK_RESULT', data)
          resolve(data)
        }).catch(err => {
          reject(err)
        })
      })
    },
  },
  namespaced: true
};
