const menu = [
  {
    path: 'home',
    name: 'Home',
    component: () => import('@/views/Home'),
    meta: {
      title: '首页',
      icon: 'bar-chart',
      auth: true
    }
  },
  {
    path: 'shop',
    component: () => import('@/views/shop'),
    redirect: '/shop/product',
    meta: {
      title: '管理中心',
      icon: 'bar-chart',
      auth: true
    },
    children: [
      {
        path: 'product',
        name: 'Product',
        redirect: 'product',
        component: () => import('@/views/shop/product'),
        meta: {
          title: '产品管理'
        },
        children: [
          {
            path: '',
            component: () => import('@/views/shop/product/Manage.vue')
          }
        ]
      },
      {
        path: 'advertisement',
        name: 'Advertisement',
        redirect: 'advertisement',
        component: () => import('@/views/shop/advertisement'),
        meta: {
          title: '广告管理'
        },
        children: [
          {
            path: '',
            component: () => import('@/views/shop/advertisement/Manage.vue')
          }
        ]
      },
      {
        path: 'order',
        name: 'Order',
        redirect: 'order',
        component: () => import('@/views/shop/order'),
        meta: {
          title: '订单管理'
        },
        children: [
          {
            path: '',
            component: () => import('@/views/shop/order/Manage.vue')
          }
        ]
      },
      {
        path: 'comment',
        name: 'Comment',
        redirect: 'comment',
        component: () => import('@/views/shop/comment'),
        meta: {
          title: '评论管理'
        },
        children: [
          {
            path: '',
            component: () => import('@/views/shop/comment/Manage.vue')
          }
        ]
      }
    ]
  },
  {
    path: 'manager',
    component: () => import('@/views/manager'),
    redirect: '/manager/category',
    meta: {
      title: '管理员管理中心',
      icon: 'bar-chart',
      auth: true,
      role: true,
    },
    children: [
      {
        path: 'category',
        name: 'Category',
        redirect: 'category',
        component: () => import('@/views/manager/category'),
        meta: {
          title: '分类管理'
        },
        children: [
          {
            path: '',
            component: () => import('@/views/manager/category/Manage.vue')
          }
        ]
      },
      {
        path: 'news',
        name: 'News',
        redirect: 'news',
        component: () => import('@/views/shop/advertisement'),
        meta: {
          title: '资讯管理'
        },
        children: [
          {
            path: '',
            component: () => import('@/views/shop/advertisement/Manage.vue')
          }
        ]
      },
      {
        path: 'shop',
        name: 'Shop',
        redirect: 'shop',
        component: () => import('@/views/manager/shop'),
        meta: {
          title: '商户管理'
        },
        children: [
          {
            path: '',
            component: () => import('@/views/manager/shop/Manage.vue')
          }
        ]
      }
    ]
  },
  {
    path: 'setting',
    name: 'Setting',
    component: () => import('@/views/Setting'),
    meta: {
      title: '个人中心',
      icon: 'setting',
      auth: true
    }
  }
]

export default menu